import axios from 'axios'

import qs from 'qs'
import { Toast } from 'antd-mobile';
// import { Actions } from 'react-native-router-flux';

// axios.defaults.baseURL = 'https://keow.net/api';
axios.defaults.baseURL = 'http://jht.forevive.com/';
// axios.defaults.baseURL = 'http://192.168.1.106:8888/api/';
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
axios.defaults.timeout = 10000;


let pending = [];
let cancelToken = axios.CancelToken;
let removeRepeatUrl = (ever) => {
    for (let p in pending) {
        if (pending[p].u === ever.url + '&' + ever.method) {
            pending[p].f();
            pending.splice(p, 1);
        }
    }
}
let login = true;
let loginMsg = true;
let exceeded = 0;

axios.interceptors.request.use(config => {
    if (/get/.test(config.method)) {
        if (!config.params && config.data) {
            config.params = config.data
            config.data = undefined
        }
    }
    if (/form/.test(config.method)) {
        config = {
            ...config,
            method: 'post',
            data: qs.stringify(config.data)
        }
    }
    // console.log(config);
    return config
}, error => {
    console.log(error)
    Promise.reject(error)
})

axios.interceptors.response.use(response => {
    // removeRepeatUrl(response.config);
    const res = response.data
    // console.log(res);
    if (res.code != 200) {
        Toast.info(res.msg, 3, null, false);
    }
    return res
}, error => {
    console.log(error)
    return Promise.reject(error)
})

export default axios