import React, { useEffect, useState, useCallback, useRef } from 'react'
import { NavBar, Icon, Button, Toast } from 'antd-mobile';
import './details.css';
import axios from 'axios';
import { withRouter } from "react-router-dom";

const Details = (props) => {
    const [list, setList] = useState([]);
    useEffect(() => {
        axios({
            method: 'get',
            url: '/api/order/getPayedOrderDetail',
            data: {
                orderId: sessionStorage.orderId,
                orderStatus: sessionStorage.orderStatus
            }
        }).then(res => {
            if (res.code == 200) {
                setList(res.data.orderDetailBeanList)
            }
        });
    }, []);
    const btn = useCallback(() => {
        axios({
            method: 'form',
            url: '/api/order/outOrder',
            data: {
                orderId: sessionStorage.orderId,
            }
        }).then(res => {
            if (res.code == 200) {
                Toast.success('出单成功', 3,null,false);
                props.history.goBack()
            }
        });
    }, []);
    return <div className='body-box'>
        <NavBar
            icon={<Icon type="left" className='k-d-ico' />}
            onLeftClick={() => props.history.goBack()}
            mode="light" >订单详情</NavBar>

        <div className='k-d-cont-box'>
            <div className='k-d-big-box'>
                <div className='k-d-box'>
                    <div className='k-d-child1'>名称</div>
                    <div className='k-d-child2'>杯数</div>
                    <div className='k-d-child3'>备注</div>
                </div>
                {
                    list.map((item, i) => {
                        return <div key={i}>
                            <div className='k-d-box2'>
                                <div className='k-d-child1'>{item.productName}</div>
                                <div className='k-d-child2'>x{item.productNum}</div>
                                <div className='k-d-child3'>{item.productAttr}</div>
                            </div>
                            <div className='k-d-xian'></div>
                        </div>
                    })
                }
            </div>
            <Button  className={`k-d-btn ${sessionStorage.orderStatus == 1 ? '' : 'k-d-btn2'}`} onClick={() => btn()} disabled={sessionStorage.orderStatus == 1 ? false : true}>{sessionStorage.orderStatus == 1 ? '出单' : '已出单'}</Button>
        </div>
    </div>
}
export default withRouter(Details);