import React, { useEffect, useState, useCallback, useRef } from 'react'
import { NavBar, } from 'antd-mobile';
import axios from 'axios';
import { withRouter } from "react-router-dom";
import './kichen.css';
import img from '../assets/img/xiadanok.png'
import img2 from '../assets/img/xiadanOKico.png'


const Kitchen = (props) => {

    const [btnIndex, setBtnIndex] = useState(1)
    const btnIndexRef = useRef(1);

    const [list, setList] = useState([]);
    const ajax = useCallback((orderStatus) => {
        axios({
            method: 'get',
            url: '/api/order/getOrderPayedList',
            data: {
                pageNum: 1,
                pageSize: orderStatus==1?800:1500,
                orderStatus
            }
        }).then(res => {
            if (res.code == 200) {
                setList(res.data.listReult)
            }
        });
    }, [])
    useEffect(() => {
        ajax(1);
    }, []);
    useEffect(() => {
        const id = setInterval(() => {
            ajax(btnIndexRef.current);
        }, 5000);
        return () => clearInterval(id);
    }, []);

    const btn = useCallback((orderStatus) => {
        setBtnIndex(orderStatus)
        btnIndexRef.current = orderStatus;
        ajax(orderStatus);
    }, [])
    return <div>
        <NavBar mode="light" >后厨</NavBar>
        <div className="k-big-box2">
            <div className={`k-b2-btn1 ${btnIndex == 1 ? 'active' : ''}`} onClick={() => { btn(1) }}>未出单</div>
            <div className={`k-b2-btn1 ${btnIndex == 2 ? 'active' : ''}`} onClick={() => { btn(2) }}>已出单</div>
        </div>
        <div className='k-big-box'>
            {
                list.map((item, i) => {
                    return <div className={`k-box ${item.orderStatus == 2 ? 'active' : ''}`} key={i} onClick={() => {
                        sessionStorage.orderId = item.masterId;
                        sessionStorage.orderStatus = item.orderStatus;
                        props.history.push('/details');
                    }}>
                        <img className="k-box-img" src={img} alt="" />
                        <img className="k-box-img2" src={img2} alt="" />
                        <div className='k-title'> 桌号{item.tableNo} </div>
                        <div className='k-text1'>{item.payTime.split(' ')[1]}</div>
                        <div className='k-text2'>{item.payTime.split(' ')[0]}</div>
                    </div>
                })
            }
        </div>

    </div>
}

export default withRouter(Kitchen);